body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    text-align: center; 
  }
  title {
    margin: auto;
    text-align: center;
  }
  ol, ul {
    padding-left: 30px;
  }
  button {
    margin-top: 5px;
    margin-bottom: 5px;
    margin-right: 5px;
    border-radius: 5%;
    padding: 1%;
    width:min-content;
    height:auto;
    background-color: aliceblue;
  }
  #list-holder{
    width:max-content;
    
  }
  p {
    text-align: justify;
  }
  li {
    text-align:justify;
  }
  .freq_slider {
    width: 100%;
  }
  .slider {
    width: 100%;
    height: 25px;
    outline:none;
    -webkit-appearance:none;
    opacity: 0.8;
    background: #ffc8dd;;
    transition: opacity .5s;
  }
  .slider:hover{
    opacity: 1;
    background-color:#ffafcc;
  }
  .seqStep {
    border-radius: 100%;
    width: 50px;
    height: 50px;
    background-color: rgb(115, 213, 226);
    opacity: 0.7;
    transition: opacity .8s;
    transition: background-color .3s;
  }
  .seqStep:hover{
    opacity: 1;
    background-color: crimson;
  }
  span {
    height:max-content;
    width:min-content;
  }
  div {
    margin:auto;
    padding: 2%;
    text-align: center;
  }
  .mainDiv {
    margin:auto;
    width:max-content;
    height:min-content;
    padding: 2%;
    border-radius: 2%;
    background-color:#a2d2ff;
    box-shadow: 5px 5px 5px 5px grey;
  }
  .inlineDiv {
    width:220px;
  }
